<template>
    <v-card style="background-color: #121212">
        <v-card-text>
            <v-row>
                <v-col cols="12" md="6" lg="5" v-if="!clicked">
                    <v-img v-on:click="clicked = true" :src="`https://img.youtube.com/vi/${selected_vid.id}/mqdefault.jpg`" contain >
                        <div style="background-color: rgba(0, 0, 0, 0.6); height: 100%" class="text-center">
                            <v-icon style="margin-top: 20%; font-size: 64px">play_circle_outline</v-icon>
                        </div>
                    </v-img>
                </v-col>

                <v-col cols="12" md="6" lg="5" v-if="clicked">
                    <iframe width="100%" height="320" :src="`https://www.youtube-nocookie.com/embed/${selected_vid.id}/?modestbranding=1`"
                            frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope;" allowfullscreen></iframe>
                </v-col>

                <v-col cols="12" md="6" lg="5">
                    <h3 v-text="vid_obj.title"></h3><br>
                    <p v-html="vid_obj.description"></p><br>
                    <p v-if="vid_obj.playlist.length > 1">Progression journey: </p>
                    <v-btn class="ma-2" rounded v-for="(v, i) in this.vid_obj.playlist" :key="i" @click="selected_vid = v" :outlined="!(selected_vid === v)">{{ v.title }}</v-btn>
                </v-col>
            </v-row>
        </v-card-text>
    </v-card>
</template>

<script>
    export default {
        name: "YouTubeVideoComponent",
        props: ["vid_obj"],
        data: () => ({
            clicked: false,
            selected_vid: {}
        }),
        mounted() {
            this.selected_vid = this.vid_obj.playlist[0];
        }
    }
</script>
